#main-nav {
  position: fixed;
}
.dropdown-toggle {
  border: none !important;
  background-color: transparent !important;
  font-size: 0.9em !important;
  box-shadow: none !important;
  font-family: Arial, sans-serif !important;
}
nav {
  background: rgba(255, 255, 255, 0.65);
  height: 100px;
  position: fixed;
  z-index: 6;
  border: none !important;
  box-shadow: none !important;
}
.nav-link {
  font-size: 1.2em;
}
nav a {
  color: #000 !important;
}
.navbar-collapse {
  margin-left: 74%;
}
@media (max-width: 480px) {
  nav {
    height: 80px;
  }
  .mobile-nav-links {
    background: rgb(255, 255, 255);
    position: fixed;
    z-index: 9999;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    padding-left: 2%;
  }
  .navbar-toggler:focus {
    background: #316b22;
    color: #fff !important;
  }

  .nav-link {
    display: inline-block;
    margin: 0 auto;
    margin-left: 5%;
  }
  .mobile-nav-links .nav-link:first-of-type {
    margin-left: 15%;
  }
  .nav-link:active {
    background: transparent;
  }

  .navbar-toggler {
    z-index: 99999;
  }
  #basic-navbar-nav {
    left: 0;
    position: fixed;
    top: 80px;
  }

  .navbar-brand {
    font-size: 0.8em;
  }

  .call-to-action {
    grid-row: 2;
  }
  #cta-phone {
    grid-column: 1 / span 3;
  }
  #cta-email {
    grid-column: 3 / span 2;
    margin: auto 0;
  }
  #account-toggle {
    position: fixed;
    right: 17px;
    top: 97px;
    z-index: 9999;
    grid-column: 4 / span 5;
    grid-row: 3;
  }
  .account-dropdown {
    position: fixed !important;
    width: 100%;
    top: 232px;
  }
  #book-now-btn {
    grid-column: 3 / span 4;
  }
  #nav-phone,
  #nav-mail {
    font-size: 3em;
    color: #316b22 !important;
    margin-top: 30%;
  }
  #nav-phone {
    font-size: 3.3em;
  }
}
@media (min-width: 480px) {
  #main-nav {
    padding: 4% 2% 3% 2%;
  }
  .desktop-nav-items {
    display: flex;
    position: absolute;
    right: 2%;
  }
}
